<template>
  <img src="./logo.png" alt="">
  <h1>Hello Vue 3</h1>
  <button @click="increase">点击量：{{ count }}次</button>
</template>

<script>
import { ref } from 'vue'
export default {
  setup() {
    const count = ref(0)

    const increase = () => count.value++

    return {
      count,
      increase
    }
  }
}
</script>
<style scoped>

img {
  width: 200px;
  text-align: center;
}

h1 {
  font-size: 18px;
}


</style>